<template>
  <div class="Login">
    <div class="contents">
      <div class="logo"></div>
      <div class="login-form">
        <p class="login-subtitle"> </p>
        <!--div class="login-field id">
          <input type="text" name="id" v-model="username" class="input-field" placeholder="  " />
        </div>
        <div class="login-field pw">
          <input type="password" name="pw" v-model="password" class="input-field"  placeholder=" " />
        </div>
        <button type="button" class="login-submit" @click="goLogin()"></button-->
        <!--button type="button"  @click="goKakao()"> </button-->
        <div class="login-kakao">
          <KakaoLogin
            api-key="efda991ab184221e23ed706a08ab4488"
            image="kakao_account_login_btn_medium_wide"
            :on-success="onSuccess"
            :on-failure="onFailure"
            error-message="      .   ."
            />
          </div>
      </div>
      <!--div class="bottom">
        <button type="button" class="button-findpw"> </button>
        <button type="button" class="button-join" @click="goJoin()"></button>
      </div-->
    </div>
  </div>
</template>

<script>
import KakaoLogin from 'vue-kakao-login'
import API from '@/api.js'

export default {
  name: 'Login',
  components: {
    'KakaoLogin' : KakaoLogin
  },
  data() {
    return {
      token : 'TEST_ACCESS',
      username : '',
      password : ''
    }
  },
  methods: {
    goLogin () {
      API.auth({username: this.username, password: this.password}, '/login')
        .then(res => {
          if(!res.data.success) throw new Error(res.data.msg);
          if(res.data.token != undefined){
            this.$store.dispatch('LOGIN', {accessToken : res.data.token, userName : res.data.username})
            .then(() => {
              alert("   .\n(  :" + new Date() +" )");
              this.$router.push({name: 'List'});
            });
          }
        }).catch((e) => {
          alert("  ." + e.message);
        });
    },
    onSuccess (res) {
      let $this = this;
      API.auth({ 'token' : res.access_token }, '/sns').then(user => {
        $this.$store.dispatch('LOGIN', {accessToken : res.access_token, userName : user.data.username})
        .then(() => {
          alert("   .\n(  :" + new Date() +" )");
          $this.$router.push({name: 'List'});
        });
      })
    },
    onFailure (res) {
      console.warn(res.error_description);
    },
    goJoin() {
      this.$router.push({name: "Join"});
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/login.scss";
</style>
